.contacts {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.contacts-info a,
.contacts-info p {
  color: @special-pink;
  font-style: normal;
  text-decoration: none;
  font-weight: 300;
  font-size: 24px;
  line-height: 27px;
}

.contacts-info p {
  margin-top: 0;
}

.contacts__title {
  color: @dark-blue;
  font-weight: 300;
  font-size: 24px;
  margin-bottom: 30px;
}

.contacts h3 {
  color: @dark-blue;
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 0;
}
